'use client';

import { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { useClient } from './ClientContext';
import { useAssistant } from './AssistantContext';
import { Thread, ThreadContextType } from '@/types/chat';

const ThreadContext = createContext<ThreadContextType | undefined>(undefined);

export function ThreadProvider({ children }: { children: ReactNode }) {
  const client = useClient();
  const { selectedAssistantId } = useAssistant();
  const [threads, setThreads] = useState<Thread[]>([]);
  const [currentThreadId, setCurrentThreadId] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(false);

  // Load threads when the component mounts
  useEffect(() => {
    loadThreads();
  }, []);

  const loadThreads = async () => {
    if (!client) return;

    try {
      setIsLoading(true);
      const response = await fetch('/api/threads');
      if (!response.ok) {
        throw new Error('Failed to load threads');
      }
      const data = await response.json();
      setThreads(data.threads || []);
    } catch (error) {
      console.error('Error loading threads:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const createNewThread = async () => {
    if (!client || !selectedAssistantId) {
      throw new Error('Client or assistant not initialized');
    }

    try {
      setIsLoading(true);
      const response = await fetch('/api/threads', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          assistant_id: selectedAssistantId,
          metadata: {
            assistant_id: selectedAssistantId,
            created_at: new Date().toISOString()
          }
        }),
      });

      if (!response.ok) {
        throw new Error('Failed to create thread');
      }

      const { thread_id } = await response.json();
      setCurrentThreadId(thread_id);
      await loadThreads(); // Refresh the threads list
      return thread_id;
    } catch (error) {
      console.error('Error creating thread:', error);
      throw error;
    } finally {
      setIsLoading(false);
    }
  };

  const loadThreadHistory = async (threadId: string) => {
    if (!client) return;

    try {
      setIsLoading(true);
      const response = await fetch(`/api/threads/${threadId}/history`);
      if (!response.ok) {
        throw new Error('Failed to load thread history');
      }
      const { messages } = await response.json();
      // Update the thread's messages in the threads array
      setThreads(prevThreads => {
        return prevThreads.map(thread => {
          if (thread.thread_id === threadId) {
            return { ...thread, messages };
          }
          return thread;
        });
      });
    } catch (error) {
      console.error('Error loading thread history:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const deleteThread = async (threadId: string) => {
    if (!client) return false;

    try {
      setIsLoading(true);
      const response = await fetch(`/api/threads/${threadId}`, {
        method: 'DELETE',
      });

      if (!response.ok) {
        throw new Error('Failed to delete thread');
      }

      // Remove the thread from the threads array
      setThreads(prevThreads => prevThreads.filter(thread => thread.thread_id !== threadId));
      
      // If the deleted thread was the current thread, clear the current thread ID
      if (currentThreadId === threadId) {
        setCurrentThreadId(null);
      }

      return true;
    } catch (error) {
      console.error('Error deleting thread:', error);
      return false;
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <ThreadContext.Provider
      value={{
        threads,
        currentThreadId,
        createNewThread,
        loadThreadHistory,
        deleteThread,
        setCurrentThreadId,
        loadThreads,
        isLoading,
      }}
    >
      {children}
    </ThreadContext.Provider>
  );
}

export function useThread() {
  const context = useContext(ThreadContext);
  if (context === undefined) {
    throw new Error('useThread must be used within a ThreadProvider');
  }
  return context;
}
