'use client';

import { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { useClient } from './ClientContext';
import type { Assistant as SDKAssistant } from '@langchain/langgraph-sdk';

interface AssistantContextType {
  assistants: SDKAssistant[];
  selectedAssistantId: string | null;
  setSelectedAssistantId: (id: string | null) => void;
  isLoading: boolean;
  error: Error | null;
}

const AssistantContext = createContext<AssistantContextType | undefined>(undefined);

export function AssistantProvider({ children }: { children: ReactNode }) {
  const client = useClient();
  const [assistants, setAssistants] = useState<SDKAssistant[]>([]);
  const [selectedAssistantId, setSelectedAssistantId] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<Error | null>(null);

  useEffect(() => {
    async function loadAssistants() {
      if (!client) return;

      try {
        setIsLoading(true);
        const data = await client.assistants.search();
        setAssistants(data);
        
        // Auto-select the first assistant if none is selected
        if (!selectedAssistantId && data.length > 0) {
          setSelectedAssistantId(data[0].assistant_id);
        }
      } catch (err) {
        setError(err instanceof Error ? err : new Error(String(err)));
      } finally {
        setIsLoading(false);
      }
    }

    loadAssistants();
  }, [client, selectedAssistantId]);

  return (
    <AssistantContext.Provider
      value={{
        assistants,
        selectedAssistantId,
        setSelectedAssistantId,
        isLoading,
        error,
      }}
    >
      {children}
    </AssistantContext.Provider>
  );
}

export function useAssistant() {
  const context = useContext(AssistantContext);
  if (context === undefined) {
    throw new Error('useAssistant must be used within an AssistantProvider');
  }
  return context;
} 